<template>
  <div>
    <mu-appbar style="width: 100%;" color="primary" class="header">
      <a href="#/WarningControl">
        <mu-button icon slot="left" class="icon" size="20">
          <mu-icon value="keyboard_arrow_left"></mu-icon>
        </mu-button>
      </a>
      <span>一级处理</span>
    </mu-appbar>
    <div style="padding: 24px;">
      <mu-row gutter>
        <mu-col span="3" sm="6" md="4" lg="3" xl="1">
          <div class="grid-cell1">编号</div>
        </mu-col>
        <mu-col span="9" sm="6" md="8" lg="9" xl="11">
          <div class="grid-cell">{{this.list2.warningid}}</div>
        </mu-col>
        <mu-col span="3" sm="6" md="8" lg="9" xl="11">
          <div class="grid-cell1">企业名</div>
        </mu-col>
        <mu-col span="9" sm="6" md="4" lg="3" xl="1">
          <div class="grid-cell">{{this.list2.restaurant}}</div>
        </mu-col>
        <mu-col span="3" sm="6" md="4" lg="3" xl="1">
          <div class="grid-cell1">区域</div>
        </mu-col>
        <mu-col span="9" sm="6" md="8" lg="9" xl="11">
          <div class="grid-cell">{{this.list2.area}}</div>
        </mu-col>
        <mu-col span="3" sm="6" md="8" lg="9" xl="11">
          <div class="grid-cell1">街道</div>
        </mu-col>
        <mu-col span="9" sm="6" md="4" lg="3" xl="1">
          <div class="grid-cell">{{this.list2.street}}</div>
        </mu-col>
        <mu-col span="3" sm="6" md="4" lg="3" xl="1">
          <div class="grid-cell1">菜系</div>
        </mu-col>
        <mu-col span="9" sm="6" md="8" lg="9" xl="11">
          <div class="grid-cell">{{this.list2.restaurant_cat}}</div>
        </mu-col>
        <mu-col span="3" sm="6" md="8" lg="9" xl="11">
          <div class="grid-cell1">设备名</div>
        </mu-col>
        <mu-col span="9" sm="6" md="4" lg="3" xl="1">
          <div class="grid-cell">{{this.list2.deviceno}}</div>
        </mu-col>
        <mu-col span="3" sm="6" md="8" lg="9" xl="11">
          <div class="grid-cell1">地址</div>
        </mu-col>
        <mu-col span="9" sm="6" md="4" lg="3" xl="1">
          <div class="grid-cell">{{this.list2.address}}</div>
        </mu-col>
      </mu-row>
      <mu-row gutter>
        <mu-col span="3" sm="6" md="4" lg="3" xl="1">
          <div class="grid-cell2">联系人</div>
        </mu-col>
        <mu-col span="9" sm="6" md="8" lg="9" xl="11">
          <div class="grid-cell">{{this.list2.restaurantperson}}</div>
        </mu-col>
        <mu-col span="3" sm="6" md="8" lg="9" xl="11">
          <div class="grid-cell2">联系电话</div>
        </mu-col>
        <mu-col span="9" sm="6" md="4" lg="3" xl="1">
          <div class="grid-cell">{{this.list2.restaurantphone}}</div>
        </mu-col>
        <mu-col span="3" sm="6" md="4" lg="3" xl="1">
          <div class="grid-cell2">超标平均浓度</div>
        </mu-col>
        <mu-col span="9" sm="6" md="8" lg="9" xl="11">
          <div class="grid-cell"></div>
        </mu-col>
        <mu-col span="3" sm="6" md="8" lg="9" xl="11">
          <div class="grid-cell2">最大值浓度</div>
        </mu-col>
        <mu-col span="9" sm="6" md="4" lg="3" xl="1">
          <div class="grid-cell blink">{{this.list2.maxconc}}</div>
        </mu-col>
        <mu-col span="3" sm="6" md="4" lg="3" xl="1">
          <div class="grid-cell2">超标开始时间</div>
        </mu-col>
        <mu-col span="9" sm="6" md="8" lg="9" xl="11">
          <div class="grid-cell">{{this.list2.starttime}}</div>
        </mu-col>
        <mu-col span="3" sm="6" md="8" lg="9" xl="11">
          <div class="grid-cell2">最后预警时间</div>
        </mu-col>
        <mu-col span="9" sm="6" md="4" lg="3" xl="1">
          <div class="grid-cell blink">{{this.list2.end}}</div>
        </mu-col>
        <mu-col span="3" sm="6" md="8" lg="9" xl="11">
          <div class="grid-cell2">预警次数</div>
        </mu-col>
        <mu-col span="9" sm="6" md="4" lg="3" xl="1">
          <div class="grid-cell blink">{{this.list2.warningnum}}</div>
        </mu-col>
        <mu-col span="3" sm="6" md="8" lg="9" xl="11">
          <div class="grid-cell2">处理状态</div>
        </mu-col>
        <mu-col span="9" sm="6" md="4" lg="3" xl="1">
          <div class="grid-cell showdow" :style="color">
            <span v-if="(this.status1==0)">{{this.status}}</span>
            <span v-if="(this.status1==1)">{{this.status}}</span>
            <span v-if="(this.status1==2)">{{this.status}}</span>
          </div>
        </mu-col>
        <mu-col span="3" offset="8" v-show="this.suibmit">
          <mu-button @click="handle">处理</mu-button>
        </mu-col>
      </mu-row>
    </div>
    <!-- <mu-flex justify-content="center">
      <a full-width @click="openFullscreenDialog2" color="info">点击了解更多或处理</a>
    </mu-flex>-->
    <div class="img">
      <div>
        &nbsp;&nbsp;&nbsp;&nbsp;
        <span class="span">现场图片</span>

        <mu-icon value="attachment" color="#ccc" size="20"></mu-icon>
      </div>
      <div class="carouselImg1" >
        <div v-for="(item,index) in list4" :key="index">
          <img :src="url+item" alt srcset>
        </div>
        
      </div>
    </div>
    <mu-container>
      <mu-expansion-panel>
        <div slot="header">一级处理结果</div>
        <!-- <textarea name="" id="textarea" cols="30" rows="10"></textarea> -->
        <div class="textarea">
          <div class="titleHandle">
            <span class="point">处理意见</span>/
            <span>描述</span>
          </div>
          <div class="">
            <textarea name="" id="textarea" cols="49" rows="8"></textarea>
          </div>
          <div class="img">
            <div>
              &nbsp;&nbsp;&nbsp;&nbsp;
              <span class="span">现场图片</span>

              <mu-icon value="attachment" color="#ccc" size="20"></mu-icon>
            </div>
            <div class="carouselImg1">
              <div  v-for="(item,index) in list3" :key="index">
                <img :src="url+item" alt srcset>
              </div>
              
            </div>
          </div>
        </div>
        <!-- <mu-button slot="action" flat>Cancel</mu-button>
        <mu-button slot="action" flat color="primary">Save</mu-button>-->
      </mu-expansion-panel>

     
    </mu-container>
  </div>
</template>
<script>
const token = sessionStorage.getItem("token");
import carouselImg1 from "../../../static/img/a.jpg";
import carouselImg2 from "../../../static/img/b.jpg";
import carouselImg3 from "../../../static/img/c.jpg";
import carouselImg4 from "../../../static/img/d.jpg";
export default {
  data() {
    return {
      suibmit: false,
      color: "",
      index: 0,
      msg: "hello 数据详情",
      value1: undefined,
      value2: undefined,
      value3: undefined,
      carouselImg1,
      carouselImg2,
      carouselImg3,
      carouselImg4,
      openFullscreen2: false,
      openFullscreen1: false,
      list2: [],
      list3:[],
      list4:[],
      warningid: "",
      status: "",
      status1: 0,
      url:"http://47.112.16.168:8888/"
    };
  },
  methods: {
    initStatus() {
      var yistatus = this.list2.yijichuli.status;
      var erstatus = this.list2.erjichuli.status;
      var sanstatus = this.list2.sanjichuli.status;
      if (yistatus == 2 || erstatus == 2 || sanstatus == 2) {
        this.status = "已处理";
        this.status1 = 2;
        this.color = "text-shadow:5px 2px 6px #4caf50;";
        this.suibmit = false;
      } else if (yistatus == 0 && erstatus == 0 && sanstatus == 0) {
        this.status = "未处理";
        this.status1 = 0;
        this.color = "text-shadow:5px 2px 6px #f44336;";
        this.suibmit = false;
      } else {
        this.status = "待处理";
        this.status1 = 1;
        this.color = "text-shadow:5px 2px 6px #ff9800;";
        this.suibmit = true;
      }
      //  else if((yijichuli==1)&&(erjichuli==0)&&(sanstatus==0)){

      //  }
      //  else if((yijichuli==1)&&(erjichuli==1)&&(sanstatus==0)){

      //  }
      // //  else if((yijichuli==1)&&(erjichuli==2)&&(sanstatus==0)){

      // //  }
      //  else if((yijichuli==1)&&(erjichuli==0)&&(sanstatus==0)){

      //  }
      console.log(this.status, this.status1);
    },
    handle() {
      this.$router.push({ path: "/Handle", query: { id: this.index,warningid:this.warningid } });
    },
    openFullscreenDialog2() {
      this.openFullscreen2 = true;
    },
    closeFullscreenDialog2() {
      this.openFullscreen2 = false;
    },
    httpDetails() {
      var _this = this;
      // var name = "谢氏烧烤摊";
      console.log("e");
      var url =
        "http://47.112.16.168:8800/warning/see?warningid=" + this.warningid;
      // ?username="+username+"password="+password
      this.$http
        .get(
          url,
          // {
          //   name
          // },
          { headers: { token: token } }
        )
        .then(res => {
          _this.list2 = res.data.data;
          console.log("list2",_this.list2);
          _this.list3 = _this.list2.yijichuli.photo1
          _this.list4 = _this.list2.warningphoto
          $("#textarea").val(_this.list2.yijichuli.pd)
          this.initStatus();
        })
        .catch(res => {
          console.log(res);
        });
    }
  },
  created() {
    this.warningid = this.$route.query.warningid;
    console.log(this.warningid);
    this.httpDetails();
  }
};
</script>
<style scoped>
.grid-cell {
  text-decoration: blink;
}
.blink {
  font-size: 16px;
  color: #dd1b14;
  animation: blink 1s linear infinite;

  -webkit-animation: blink 1s linear infinite;
  -moz-animation: blink 1s linear infinite;
  -ms-animation: blink 1s linear infinite;
  -o-animation: blink 1s linear infinite;
}
@keyframes blink {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.demo-list-wrap {
  width: 100%;
  max-width: 360px;
  overflow: hidden;
}
.showdow {
  text-shadow: 5px 2px 6px #4caf50;
}
.carouselImg1 img {
  width: 20%;
}
.carouselImg1 {
  display: flex;
  flex-direction: row;
  width: 98vw;
  padding: 0 10px;
}
.carouselImg1 div {
  width: 25%;
}
.carouselImg1 img {
  width: 99%;
}
.img {
  /* text-indent: 1px; */
}
.span {
  font-size: 16px;
  font-weight: 600;
  border-left: 4px rgb(67, 191, 240) solid;
}
.textarea {
  width: 100%;
  height: auto;
}
.titleHandle {
  font-weight: 600;
}
.titleHandle .point {
  color: blue;
  font-size: 17px;
}
.contentHandle {
  width: 100%;
  height: 180px;
  border: 1px #ccc solid;
}
.mu-expansion-panel-content{
  padding: 0;
}
</style>



